*{
    box-sizing: border-box;
}
body{
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3fe;
    height: 100%;
}

html, body, #root, .container{
    height: 100%;
}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
}

.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    width: 8 0%;
    padding: 50px;
}

.movies .movie{
    width: 45%;
    position: relative;
    background-color: white;
    color: #b2b3bc;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 20px;
    font-weight: 300;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}


.movie img{
    position: relative;
    top: -30px;
    max-width: 150px;
    width: 100%;
    margin-right: 20px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);

}

.movie 
.movie_title,
.movie_year {
    margin: 0;
    font-weight: 500;
}

.movie .movie_title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #5a4e4e;
}

.movie_genres{
    padding: 0;
    margin: 5px 0px;
    list-style: none;
    display: flex;
}

.movie_genres li{
    margin-right: 10px;
    font-size: 14px;

}